@import url(https://fonts.googleapis.com/css?family=Muli&display=swap);
.header{-webkit-align-items:center;align-items:center;background:#568a5f;color:white;display:-webkit-flex;display:flex;height:100px;-webkit-justify-content:space-between;justify-content:space-between;top:0px;width:100%}.header__img{margin-left:30px;width:200px}.header__menu{margin-right:30px}.header__menu ul{display:none;list-style:none;margin:0px 0px 0px -14px;padding:0px;position:absolute;width:100px;text-align:right}.header__menu:hover ul,ul:hover{display:block}.header__menu li{margin:10px 0px}.header__menu li a{color:white;text-decoration:none}.header__menu li a:hover{text-decoration:underline}.header__menu--profile{-webkit-align-items:center;align-items:center;display:-webkit-flex;display:flex;cursor:pointer}.header__menu--profile img{margin-right:8px;width:40px}

.main{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;-webkit-flex-direction:column;flex-direction:column;height:300px;border-radius:0px 0px 40px 40px}.main__title{color:white;font-size:3rem}

.categories__title{background:#568a5f;color:white;font-size:2rem;margin-top:15px;position:absolute;padding-left:30px;width:100%}

.carousel{overflow:scroll;padding-left:30px;width:100%;position:relative;margin-bottom:15px;overflow-y:hidden}.carousel__container{transition:450ms -webkit-transform;transition:450ms transform;transition:450ms transform, 450ms -webkit-transform;font-size:0;white-space:nowrap;margin:70px 0px;padding-bottom:10px}.carousel__container:hover .carousel-item{opacity:0.3}.carousel__container:hover .carousel-item:hover{-webkit-transform:scale(1.5);transform:scale(1.5);opacity:1}

.carousel-item{border-radius:20px;overflow:hidden;position:relative;display:inline-block;width:200px;height:250px;margin-right:10px;font-size:20px;cursor:pointer;transition:450ms all;-webkit-transform-origin:center left;transform-origin:center left}.carousel-item:hover ~ .carousel-item{-webkit-transform:translate3d(100px, 0, 0);transform:translate3d(100px, 0, 0)}.carousel-item__img{width:200px;height:250px;object-fit:cover}.carousel-item__details{-webkit-align-items:flex-start;align-items:flex-start;background:linear-gradient(to top, rgba(0,0,0,0.9) 0%, rgba(0,0,0,0) 100%);bottom:0;display:-webkit-flex;display:flex;font-size:10px;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:flex-end;justify-content:flex-end;left:0;opacity:0;transition:450ms opacity;padding:10px;position:absolute;right:0;top:0}.carousel-item__details--img{width:20px}.carousel-item:hover .carousel-item__details{opacity:1}.carousel-item__img{width:200px;height:250px;object-fit:cover}.carousel-item__details--title{color:white;margin:5px 0px 0px 0px;font-size:0.9rem}.carousel-item__details--subtitle{color:white;font-size:8px;margin:3px 0px}.carousel-item__details--info{font-size:0.5rem}

.footer{-webkit-align-items:center;align-items:center;background:#568a5f;display:-webkit-flex;display:flex;height:100px;width:100%}.footer a{color:white;cursor:pointer;font-size:14px;padding-left:30px;text-decoration:none}.footer a:hover{text-decoration:underline}

@media only screen and (max-width: 600px){.main__description--title{font-size:30px}.main__title{font-size:1.5rem}.footer{-webkit-align-items:flex-start;align-items:flex-start;-webkit-flex-direction:column;flex-direction:column}}*{box-sizing:border-box}body{margin:0;font-family:"Muli",sans-serif;background:#568a5f}

.container-info__perfil{max-height:170px;max-width:350px;background-color:white;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;margin:auto;border-radius:25px}.info-perfil{height:inherit;width:inherit;text-align:center}@media only screen and (max-width: 600px){.container-info__perfil{height:170px;width:300px;margin:auto;height:170px}}

